.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 85vh;
flex-direction: column;
}
.wrapper .container_form{
    width: 50%;
    height: 50%;
    border: 2px lightgrey solid;
}
.wrapper form{

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
}
.submit{
    margin-top: 5%;
    width: 50%;
    display: flex;
    justify-content: space-between;
}
.submit button{
    margin-left: 5%;
}
.submit input{
    position: relative;
    top: 2px;
    left: 2px;
}
.red{
    color: red;
    font-size: 0.7rem;
}
.newUser{
    color: green;
    font-weight: bold;
}